
import Group from 'vux/src/components/group/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import Scroll from './pulldown'

export default {
components: {
Group,
LoadMore,
Cell,
XButton,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Popup,
XInput,
'v-scroll': Scroll
},
data () {
return {
show: false,
pageNum: 1, // 
pageSize: 15,  // 
loadmore: false,
loadbottom: false,
investorList: [],
mobile: '',
name: ''
}
},
mounted () {

this.getInvestorList(this.pageNum, this.pageSize)
},
methods: {
add () {
this.show = true
},
save () {
if (!this.$refs.mobile.valid) {
this.$vux.toast.text('!', 'middle')
return
}
if (!this.$refs.name.valid) {
this.$vux.toast.text('!', 'middle')
return
}
this.show = false
var that = this
var params = 'mobile=' + this.mobile + '&name=' + this.name
this.post('/api/auth/investor/add_investor', params, function (response) {
that.mobile = ''
that.name = ''
that.getInvestorList(that.pageNum, that.pageSize)
})
},
del (val) {
var that = this
var params = 'id=' + val
this.post('/api/auth/investor/delete_investor', params, function (response) {
that.getInvestorList(that.pageNum, that.pageSize)
})
},
getInvestorList (pageNum, pageSize) {
var that = this
this.loadmore = true
var params = 'pageNum=' + pageNum + '&pageSize=' + pageSize
this.noLoadPost('/api/auth/investor/get_investor_list', params, function (response) {
that.investorList = response.data
that.loadmore = false
})
},
onRefresh (done) {
this.loadbottom = false
this.investorList = []
this.pageNum = 1
this.getInvestorList(1, 15)
done()
},
onInfinite (done) {
if (this.loadbottom === true) {
done()
return
} else {
var that = this
this.loadmore = true
this.pageNum++
let pageNum = this.pageNum
var params = 'pageNum=' + pageNum + '&pageSize=' + this.pageSize
this.noLoadPost('/api/auth/investor/get_investor_list', params, function (response) {

that.downdata = response.data
that.investorList = that.investorList.concat(that.downdata)
if (that.downdata.length === 0) {
that.loadbottom = true
}
that.loadmore = false
done()
})
}
}
},
computed: {

}
}



